body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.MDEditor-textarea {
  height: 20em;
  min-height: 10em;
}

.MDEditor-preview {
  min-height: 10em;
  flex-grow: 1;
}

.MDEditor-preview blockquote {
  margin-left: 2.5rem;
}

.MDEditor-preview img {
  max-width: 100%;
}

.MDEditor-btn {
  color: #fff;
  background-color: hsl(200, 100%, 42%);
  border-color: hsl(200, 100%, 42%);
}
.App {
  text-align: center;
  background: -webkit-linear-gradient(hsla(203, 63%, 10%, 50%), hsl(187, 100%, 61%, 50%));
  background: linear-gradient(hsla(203, 63%, 10%, 50%), hsl(187, 100%, 61%, 50%));
  background-attachment: fixed;
}

.App-header {
  background-color: hsl(194, 15%, 38%);
  color: white;
}

.App-title {
  font-size: 1.5em;
}

@media screen and (min-width: 768px) {
  .App-title {
    font-size: 2.5em;
  }
}

.App-title-markdown {
  font-family: 'Inconsolata', monospace;
  margin-bottom: .4em; /* must be in em not rem to maintain proportions when changing font size */
}

.App-title-previewer {
  font-family: 'Courgette', cursive;
}

.App-footer {
  background-color: hsl(187, 51%, 42%);
  color: hsl(210, 100%, 18%);
}

.App-footer a {
  color: white;
}
